import { FC, ChangeEvent, useRef } from "react";
import styled from "@emotion/styled";
import { boxShadow } from "../styles";
const Wrapper = styled.label`
& input {
    display: none;
}
`;

const VisiblePart = styled.label`
display: inline-block;
user-select: none;
cursor: pointer;
text-align: center;
border-radius: 0.5rem;
width: 2.5rem;
height: 2.5rem;
color: rgba(0,0,0,0);
background: ${({ theme }) => theme.components.background};
background: ${({ theme }) => boxShadow(theme.components.shadow1, theme.components.shadow2)};
`;

type Props ={
    onChange: (event: ChangeEvent<HTMLInputElement>) => void;
};

export const Checkbox: FC<Props> =({ onChange }) => {
    const { current: fieldId } = useRef(
        `prefix-${Math.random().toString(16).slice(2)}`
    )

    return(
        <Wrapper>
            <input id={fieldId} type="checkbox" onChange={onChange} />
            <VisiblePart htmlFor={fieldId}>✔️</VisiblePart>
        </Wrapper>
    );
};